.post-list-item {
  display: flex;
  cursor: pointer;
}

.post-list-item:hover {
  background-color: var(--list-hover-background-color);
  transition: all 0.3s ease-in;
}

.post-list-item-info {
  word-break: break-all;
}

.post-list-item-info h2 {
  margin-top: 0;
}

.post-list-item-thumb {
  width: 100%;
  padding-top: 50%;
  padding-bottom: 50%;
  background-size: cover;
}

@media (min-width: 992px) {
  .post-list-item {
    padding: 20px;
  }
  .post-list-item-info {
    flex: 0 0 60%;
  }
  .post-list-item-thumb-wrapper {
    flex: 0 0 40%;
    width: 40%;
    padding: 20px;
    max-width: 250px;
    max-height: 250px;
    align-self: center;
  }
}

@media (max-width: 992px) {
  .post-list-item {
    flex-direction: column-reverse;
    margin: 10px 0;
    padding: 10px 0;
    border-top: 1px dashed #999;
  }
  .post-list-item-info {
    flex: 0 0 100%;
  }
  .post-list-item-thumb-wrapper {
    flex: 0 0 100%;
  }
}